<template>
  <LvColorpicker
    v-model="color"
    :label="label"
    :withoutInput="withoutInput"
    :hidePalette="hidePalette"
    :colors="colors"
    :clearable="clearable"
/>
</template>

<script>
import LvColorpicker from 'lightvue/color-picker';

export default {
  name:"color-picker",
  components: {
    LvColorpicker
  },
  props:{
    value:{ //
      type:String,
      default:"#000000"
    },
    label:{ //
      type:String,
      default:""
    },
    colors: { // 
      type: Array,
      default: () => [
          '#F44336', '#E91E63', '#9C27B0', '#673AB7', '#3F51B5',
          '#2196F3', '#03A9F4', '#00BCD4', '#009688', '#4CAF50',
          '#8BC34A', '#CDDC39', '#FFEB3B', '#FFC107',
          '#FF9800', '#795548'
      ]
    },
    hidePalette: {//
      type:Boolean,
      default:false
    },
    withoutInput:{ //
      type:Boolean,
      default:false
    },
    clearable :{ //
      type:Boolean,
      default:true
    },
  
  },
  emits: ['change'],
  data() {
    return {
      color: this.value,
      oldColorValue: this.value  // 
    }
  },
  watch: {
    value: {
      handler(newValue, oldValue) {
        if (newValue !== this.color) {
          this.oldColorValue = this.color; // 
          this.color = newValue; // 
        }
      }
    },
    color: {
      handler(newValue, oldValue) {
        if (newValue !== oldValue) { // 
          const changeEvent = {
            oldColorValue: this.oldColorValue, // 
            newColorValue: newValue // 
          };
          this.oldColorValue = newValue; // oldColorValue
          this.$emit('change', changeEvent);
          this.$emit("update:value", newValue);
          //console.log(changeEvent);
        }
      }
    }
  }
}
</script>

<style>
.light-icon-x::before {
content: ""; /*  */
font-size: 20px; 
display: flex; /*  Flex  */
justify-content: center; /*  */
align-items: center; /*  */
}
</style>